<template>
  <div>
    <Row>
      <meta name="referrer" content="never" />
      <Col span="22" class="a1" offset="1">
        <Tabs value="name1">
          <TabPane label="今日推荐" name="name1">
            <ul class="a2">
              <li v-for="(item,index) in asd">
                <img :src="item.target.cover_url" alt />
                <p>{{item.title}}</p>
                <p>票价：</p>
              </li>
            </ul>
          </TabPane>
          <TabPane label="即将开售" name="name2">
            <ul class="a2">
              <li v-for="(item,index) in asd1">
                <img :src="item.target.cover_url" alt />
                <p>{{item.title}}</p>
                <p>票价：</p>
              </li>
            </ul>
          </TabPane>
        </Tabs>
      </Col>
    </Row>
  </div>
</template>
<style lang="less" scoped>
.a1 {
  background-color: white;
  margin-top: 25px;
}
.a2 {
  display: flex;
  list-style: none;
  li {
    width: 20%;
    padding: 0 50px 0 10px;
    img {
      width: 100%;
    }
  }
}
</style>

<script>
export default {
  data() {
    return {
      asd: [],
      asd1: []
    };
  },
  methods: {
    get() {
      var api = "data/homeData.json";
      this.axios.get(api).then(res => {
        this.asd = res.data.recommend_feeds.slice(0, 6);
      
      });
    },
    get1() {
      var api = "data/homeData.json";
      this.axios.get(api).then(res => {
        this.asd1 = res.data.recommend_feeds.slice(6, 12);
        
      });
    }
  },

  mounted() {
    this.get();
    this.get1();
  }
};
</script>